<template>
    <div id="d">
        <div class="dropdown-wrapper">
            <div @click="isSearchVisble = !isSearchVisble" class="selected-item">
                <span class="text-sm" v-if="selectedItem">{{ selectedVal(selectedItem) }}</span>
                <span class="text-sm" v-else>{{ title }}</span>
                <svg :class="isSearchVisble ? 'dropdown' : ''" class="drop-down-icon w-5 h-5" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M11.9997 10.8284L7.04996 15.7782L5.63574 14.364L11.9997 8L18.3637 14.364L16.9495 15.7782L11.9997 10.8284Z"></path></svg>
            </div>
            <div :class="isSearchVisble ? 'visible' : 'invisible'" class="dropdown-popover">
                <input v-model="searchQuery" placeholder="" type="text">
                <span v-if="filteredData.length === 0">No Data Available</span>
                <div class="options">
                    <ul>
                        <li @click="selectItem(i)" v-for="(i , index) in filteredData" :key="`user-${index}`">
                            <img v-if="image != null" :src="i[image]" />
                            <img v-if="flag == true" :src="`https://flagsapi.com/${i.iso}/flat/64.png`" />
                            <span>{{ selectedVal(i) }}</span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
    
  </template>
  <script>
  
  export default {
    props: [
    'data',
    'dataValue',
    'title',
    'optionText',
    'image',
    'flag',
    'selectedValue',
    'selectedKeyword'
  ],
    data() {
      return {
        searchQuery: '',
      selectedItem: null,
      isSearchVisble: false,
      searchText: "",
      }
    },
    computed: {
      filteredData(){
        const query = this.searchQuery.toLowerCase();
        if(this.searchQuery == "") {
            if(this.selectedValue){
                function filter(arr, key, value) {
                return arr.reduce((data, item) => {
                    if (item[key] == value) data.push(item);
                    return data;
                }, []);
                }
                const filteredDataWithId = filter(this.data, this.selectedKeyword, this.selectedValue)
                this.selectedItem = filteredDataWithId[0]
            }
            return this.data;
        }

        // if(true){
            // this.selectItem(this.mode)
            // let grouped = _.groupBy(this.data, function(course) {
            //     return course.id;
            // });
            // console.log("gf")
            // this.selectedItem = {id: 3, company_id: 1, category_name: 'jjjjj'}
        // }
        
        return this.data.filter((item) => {
            return Object.values(item).some((word) => 
            String(word).toLowerCase().includes(query)
            );
            })
    }
    },
    methods: {
      selectItem(i){
            this.selectedItem = i
            this.isSearchVisble = false
            // this.$emit('input', this.content)
            this.$emit('update', i)
            // this.dataValue = i.id
        },
        selectedVal(obj){
                const path = this.optionText;
                const keys = path.split('.');
                let selectedValu = obj;

                for (const key of keys) {
                selectedValu = selectedValu[key];
                }

                return selectedValu;
        },

        close (e) {
      if (!this.$el.contains(e.target)) {
        this.isSearchVisble = false
      }
    },

    closeIfClickedOutside(event) {

if (!document.getElementById('dropdown').contains(event.target)) {
    this.isSearchVisble = false;
    this.searchQuery = ""
}
},
    },
    mounted() {
        console.log(this.optionText)
        console.log(typeof this.optionText)
        document.addEventListener('click', this.closeIfClickedOutside)
    },
    beforeDestroy () {
    document.removeEventListener('click',this.closeIfClickedOutside)
  },
  }
  </script>

<style scoped>
.dropdown-wrapper {
    max-width: 100%;
    position: relative;
    margin: 0 auto;
}

.selected-item {
    height: 40px;
    border: 2px solid #e2dfdf;
    border-radius: 5px;
    padding: 5px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}

.selected-item .drop-down-icon {
    transform: rotate(0deg);
    transition: all 0.4s ease;
}

.selected-item .drop-down-icon.dropdown {
    transform: rotate(180deg);
}

.dropdown-popover {
    position: absolute;
    border: 2px solid #e2dfdf;
    top: 40px;
    left: 0;
    right: 0;
    background-color: #fff;
    max-width: 150%;
    padding: 10px;
    visibility: hidden;
    transition: all .4s ease-in-out;
    max-height: 0px;
}

.dropdown-popover.visible {
    visibility: visible;
    max-height: 450px;
}

.dropdown-popover input {
    width: 100%;
    height: 30px;
    border: 2px solid #4e4ee6;
    border-radius: 5px;
    font-size: 16px;
    padding-left: 5px;
    margin-bottom: 5px;
}

.dropdown-popover input:focus {
    border: 2px solid #4e4ee6;
}

.dropdown-popover .options ul{
    list-style: none;
    text-align: left;
    padding-left: 0;
    max-height: 180px;
    overflow-y: scroll;
    overflow-x: hidden;
}

.dropdown-popover .options li{
    width: 100%;
    cursor: pointer;
    font-size: 16px;
    display: flex;
    align-items: center;
}

.dropdown-popover .options li img {
    width: 20px;
}

.dropdown-popover .options li span, 
.dropdown-popover .options li img {
    margin-left: 5px;
}

.dropdown-popover .options li:hover {
    background-color: #70878a;
    color: #fff;
    font-weight: bold;
    
}
</style>